.registry_page {
  margin: 0 auto;
  padding-top: 100px;
  width: 400px;
}

.flex_wrapper {
  display: flex;
  align-items: center;
}
.form_wrapper {
  flex: 1;
}
.item_wrapper {
  flex: 1;
  padding-right: 10px;
}
.label {
  vertical-align: middle;
  display: inline-block;
  height: 40px;
  line-height: 40px;
  margin-right: 10px;
}
.input {
  vertical-align: middle;
  height: 30px;
  padding: 5px;
  font-size: 20px;
  display: inline-block;
  height: 40px;
  line-height: 40px;
  flex: 1;
  box-sizing: border-box;
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}
.btn {
  width: 100px;
  height: 100px;
}
