@avatar_w: 40px;
.user-profile-wrapper {
    position: relative;
    width: @avatar_w;
    height: @avatar_w;
    line-height: @avatar_w;
    text-align: center;
    border-radius: 50%;
    background-color: #ccc;
    cursor: pointer;
    user-select: none;
    .draw{
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background-color: #fff;
        border: 1px solid #ccc;
    }
}
