.sidler-bar-wrapper{
    display: flex;
    flex-direction: column;
    width: 200px;
    height: 100%;
    padding-top: 50px;
    box-sizing: border-box;
    position: absolute;
    left: 0;
    top: 0;
    border-right: 1px solid #ccc;
    .sidler-bar-item{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
    }
}