.login_page {
  margin: 0 auto;
  padding-top: 100px;
  width: 400px;
}
.login-wrapper {
  display: flex;
}
.input_wrapper {
  flex: 1;
  padding-right: 10px;
}
.input {
  height: 30px;
  padding: 5px;
  font-size: 20px;
  display: inline-block;
  height: 40px;
  width: 100%;
  box-sizing: border-box;
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}
.btn {
  width: 100px;
}
